<template>
  <n-config-provider :date-locale="dateZhCN" :hljs="hljs" :locale="zhCN" :theme="theme">
    <n-modal-provider>
      <n-message-provider>
        <n-global-style/>
        <n-scrollbar>
          <n-loading-bar-provider>
            <router-view/>
          </n-loading-bar-provider>
        </n-scrollbar>
      </n-message-provider>
    </n-modal-provider>
  </n-config-provider>
</template>

<script>
import {darkTheme, dateZhCN, zhCN} from 'naive-ui'
import {configCheck} from "@/assets/js/api";
import {useRouter} from 'vue-router'
import hljs from 'highlight.js/lib/core'

document.querySelector('link[rel="icon"]').href = require('@/assets/img/JeesResourceFavicon.png')

export default {
  name: 'App',
  data() {
    return {
      theme: darkTheme,
      zhCN,
      dateZhCN,
    }
  },
  setup() {
    hljs.registerLanguage('java', require('highlight.js/lib/languages/java'))
    hljs.registerLanguage('javascript', require('highlight.js/lib/languages/javascript'))
    hljs.registerLanguage('json', require('highlight.js/lib/languages/json'))
    hljs.registerLanguage('xml', require('highlight.js/lib/languages/xml'))
    hljs.registerLanguage('yaml', require('highlight.js/lib/languages/yaml'))
    hljs.registerLanguage('shell', require('highlight.js/lib/languages/shell'))
    hljs.registerLanguage('sql', require('highlight.js/lib/languages/sql'))
    hljs.registerLanguage('go', require('highlight.js/lib/languages/go'))
    hljs.registerLanguage('python', require('highlight.js/lib/languages/python'))
    hljs.registerLanguage('php', require('highlight.js/lib/languages/php'))
    hljs.registerLanguage('c', require('highlight.js/lib/languages/c'))
    hljs.registerLanguage('bash', require('highlight.js/lib/languages/bash'))
    const router = useRouter()
    const checkConfigData = () => {
      configCheck().then(res => {
        if (res.data.code === 200) {
          if (!res.data.data) {
            router.push({name: 'install'})
          }
        } else {
          router.push({name: 'install'})
        }
      }).catch(err => {
        router.push({name: 'install'})
      })
    }
    checkConfigData()
    return {
      hljs
    }
  }
}
</script>

