import axios from 'axios'
import config from '@/config'

axios.defaults.baseURL = config.host

/**
 * 配置检查
 * @returns {Promise<axios.AxiosResponse<any>>}
 */
const configCheck = async () => {
    return await axios.get('/api/config/check/')
}

