import {createRouter, createWebHashHistory} from "vue-router";
import {adminCheckRight} from '@/assets/js/api'

const beforeEach = (to, from, next) => {
    adminCheckRight({}).then(res => {
        if (res.data.code === 200) {
            next()
        } else {
            next({name: 'login'})
        }
    }).catch(err => {
        next({name: 'login'})
    })
}
export default createRouter({
    history: createWebHashHistory(),
    routes: [
        {
          path: '/install',
          name: 'install',
          component: () => import('@/views/Install/Install.vue')
        },
        {
            path: '/',
            name: 'index',
            component: () => import('@/views/Index.vue'),
            children: [
                {
                    path: '/',
                    name: 'home',
                    component: () => import('@/views/Home/Home.vue')
                },
                {
                    path: '/resource',
                    name: 'resource',
                    query: {
                        resourceId: ''
                    },
                    component: () => import('@/views/Home/Resource.vue')
                },
                {
                    path: '/about',
                    name: 'about',
                    component: () => import('@/views/Home/About.vue')
                }
            ]
        },
        {
            path: '/login',
            name: 'login',
            component: () => import('@/views/Admin/Login.vue')
        },
        {
            path: '/admin',
            name: 'admin',
            component: () => import('@/views/Admin/Index.vue'),
            beforeEnter: beforeEach,
            children: [
                {
                    path: '/admin',
                    name: 'adminHome',
                    component: () => import('@/views/Admin/Home/Home.vue')
                },
                {
                    path: '/admin/resource',
                    name: 'adminResource',
                    component: () => import('@/views/Admin/Resource/Resource.vue')
                },
                {
                    path: '/admin/category',
                    name: 'adminCategory',
                    component: () => import('@/views/Admin/Category/Category.vue')
                },
                {
                    path: '/admin/tag',
                    name: 'adminTag',
                    component: () => import('@/views/Admin/Tag/Tag.vue')
                },
                {
                    path: '/admin/comment',
                    name: 'adminComment',
                    component: () => import('@/views/Admin/Comment/Comment.vue')
                },
                {
                    path: '/admin/user',
                    name: 'adminUser',
                    component: () => import('@/views/Admin/User/User.vue')
                },
                {
                    path: '/admin/system',
                    name: 'adminSystem',
                    component: () => import('@/views/Admin/System/System.vue')
                },
            ]
        }
    ]
})