import {createStore} from "vuex";

export default createStore({
    state: {
        hideSidebar: localStorage.getItem("hideSidebar") ? JSON.parse(localStorage.getItem("hideSidebar")) : false,
    },
    mutations: {
        setHideSidebar(state, hideSidebar) {
            state.hideSidebar = hideSidebar;
            localStorage.setItem("hideSidebar", hideSidebar);
        },
    },
    actions: {},
})